.BoardContainer {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    background-image: linear-gradient(90deg, #b7b6bb, #b7b6bb);
    align-items: center;
    -webkit-transition: background-image 0.2s ease-in-out;
    transition: background-image 0.2s ease-in-out;
}


.BoardTitle {
    position: absolute;
    top: 5px;
    width: fit-content;
    padding: 0.2rem;
    font-size: 4rem;
    text-align: center;
    border-radius: 0px;
    background-color: transparent;
    border: 0.2rem solid transparent;
    outline: none;
}

.AddNewNoteBtn,
.SettingsBoardBtn {
    position: absolute;
    padding: 0.2px;
    font-size: 2rem;
    left: 10px;
    bottom: 10px;
    background-color: transparent;
    outline: none;
    border: none;
    cursor: pointer;
    color: white;
    background-color: rgba(59, 58, 59, 0.4);
    padding: 0.3rem;
    border-radius: 5px;
    transition: all 0.1s ease-in-out;
}

.SettingsBoardBtn {
    left: 125px;
}

.AddNewNoteBtn:hover,
.SettingsBoardBtn:hover {
    background-color: rgba(59, 58, 59, 0.75);
    transition: all 0.1s ease-in-out;

}